import React from "react";
import {
  User,
  Mail,
  Lock,
  Droplet,
  Calendar,
  MapPin,
  Phone,
  Heart,
  Sun,
  Moon,
  Eye,
  EyeOff,
  Weight,
  Activity,
  Thermometer,
  Pill,
  Clock,
  AlertCircle,
  Clipboard,
} from "lucide-react";
import { motion } from "framer-motion";

const DonorRegistration = () => {
  const [isDarkMode, setIsDarkMode] = React.useState(false);
  const [showPassword, setShowPassword] = React.useState(false);
  const [formData, setFormData] = React.useState({
    // Personal Information
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    dateOfBirth: "",
    gender: "",
    phone: "",
    address: "",

    // Health Information
    bloodType: "",
    medicalHistory: "",
    currentMedications: "",
    recentIllnesses: "",
    lastDonationDate: "",
    donationFrequency: "",
    travelHistory: "",

    // Lifestyle Information
    alcoholUse: "",
    drugUse: "",
    recentTattoos: false,
    recentPiercings: false,

    // Screening Information
    bloodPressure: "",
    weight: "",
    temperature: "",
    hemoglobinLevel: "",

    // Consent
    consentGiven: false,
  });

  const toggleDarkMode = () => setIsDarkMode(!isDarkMode);

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  return (
    <motion.div
      initial="hidden"
      animate="visible"
      className={`min-h-screen w-full ${
        isDarkMode
          ? "bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900"
          : "bg-gradient-to-br from-red-50 via-white to-red-50"
      } py-12 px-4 sm:px-6 lg:px-8`}
    >
      {/* Dark Mode Toggle */}
      <motion.button
        onClick={toggleDarkMode}
        className="fixed top-4 right-4 p-2 rounded-full bg-white/80 backdrop-blur-sm shadow-lg"
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
      >
        {isDarkMode ? (
          <Sun className="h-6 w-6 text-yellow-500" />
        ) : (
          <Moon className="h-6 w-6 text-gray-800" />
        )}
      </motion.button>

      {/* Header */}
      <div className="max-w-7xl mx-auto mb-12 text-center">
        <motion.div
          className="flex justify-center"
          whileHover={{ scale: 1.1, rotate: 360 }}
          transition={{ duration: 0.5 }}
        >
          <Heart
            className={`w-16 h-16 ${
              isDarkMode ? "text-red-600" : "text-red-500"
            }`}
          />
        </motion.div>
        <h1
          className={`text-4xl font-bold ${
            isDarkMode ? "text-white" : "text-gray-900"
          }`}
        >
          Blood Donation Registration
        </h1>
        <p
          className={`mt-2 text-lg ${
            isDarkMode ? "text-gray-300" : "text-gray-600"
          }`}
        >
          Join our life-saving community
        </p>
      </div>

      {/* Main Form */}
      <div className="max-w-7xl mx-auto">
        <div
          className={`${
            isDarkMode ? "bg-gray-800/80" : "bg-white/80"
          } backdrop-blur-sm shadow-xl rounded-2xl p-8`}
        >
          <form className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Left Column - Personal Information */}
            <div className="space-y-6">
              <h2
                className={`text-xl font-semibold ${
                  isDarkMode ? "text-white" : "text-gray-900"
                } flex items-center gap-2`}
              >
                <User className="w-5 h-5" />
                Personal Information
              </h2>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    First Name
                  </label>
                  <input
                    type="text"
                    name="firstName"
                    value={formData.firstName}
                    onChange={handleInputChange}
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    Last Name
                  </label>
                  <input
                    type="text"
                    name="lastName"
                    value={formData.lastName}
                    onChange={handleInputChange}
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Gender
                </label>
                <select
                  name="gender"
                  value={formData.gender}
                  onChange={handleInputChange}
                  className={`mt-1 block w-full rounded-lg ${
                    isDarkMode
                      ? "bg-gray-900 text-white"
                      : "bg-white text-gray-900"
                  } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                >
                  <option value="">Select gender</option>
                  <option value="male">Male</option>
                  <option value="female">Female</option>
                  <option value="other">Other</option>
                </select>
              </div>

              <div className="space-y-4">
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    Email Address
                  </label>
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>

                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    Phone Number
                  </label>
                  <input
                    type="tel"
                    name="phone"
                    value={formData.phone}
                    onChange={handleInputChange}
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>

                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    Address (Optional)
                  </label>
                  <textarea
                    name="address"
                    value={formData.address}
                    onChange={handleInputChange}
                    rows={3}
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>
              </div>
            </div>

            {/* Right Column - Health Information */}
            <div className="space-y-6">
              <h2
                className={`text-xl font-semibold ${
                  isDarkMode ? "text-white" : "text-gray-900"
                } flex items-center gap-2`}
              >
                <Activity className="w-5 h-5" />
                Health Information
              </h2>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Blood Type
                </label>
                <select
                  name="bloodType"
                  value={formData.bloodType}
                  onChange={handleInputChange}
                  className={`mt-1 block w-full rounded-lg ${
                    isDarkMode
                      ? "bg-gray-900 text-white"
                      : "bg-white text-gray-900"
                  } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                >
                  <option value="">Select blood type</option>
                  {["A+", "A-", "B+", "B-", "AB+", "AB-", "O+", "O-"].map(
                    (type) => (
                      <option key={type} value={type}>
                        {type}
                      </option>
                    )
                  )}
                </select>
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Medical History
                </label>
                <textarea
                  name="medicalHistory"
                  value={formData.medicalHistory}
                  onChange={handleInputChange}
                  placeholder="List any chronic conditions, surgeries, or allergies"
                  rows={3}
                  className={`mt-1 block w-full rounded-lg ${
                    isDarkMode
                      ? "bg-gray-900 text-white"
                      : "bg-white text-gray-900"
                  } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                />
              </div>

              <div>
                <label
                  className={`block text-sm font-medium ${
                    isDarkMode ? "text-gray-300" : "text-gray-700"
                  }`}
                >
                  Current Medications
                </label>
                <input
                  type="text"
                  name="currentMedications"
                  value={formData.currentMedications}
                  onChange={handleInputChange}
                  className={`mt-1 block w-full rounded-lg ${
                    isDarkMode
                      ? "bg-gray-900 text-white"
                      : "bg-white text-gray-900"
                  } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    Weight (kg)
                  </label>
                  <input
                    type="number"
                    name="weight"
                    value={formData.weight}
                    onChange={handleInputChange}
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    Blood Pressure
                  </label>
                  <input
                    type="text"
                    name="bloodPressure"
                    value={formData.bloodPressure}
                    onChange={handleInputChange}
                    placeholder="e.g., 120/80"
                    className={`mt-1 block w-full rounded-lg ${
                      isDarkMode
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-900"
                    } border border-gray-300 px-4 py-2 focus:border-red-500 focus:ring-red-500`}
                  />
                </div>
              </div>

              <div className="space-y-4">
                <div className="flex items-center gap-2">
                  <input
                    type="checkbox"
                    name="consentGiven"
                    checked={formData.consentGiven}
                    onChange={handleInputChange}
                    className="h-4 w-4 text-red-600 focus:ring-red-500 border-gray-300 rounded"
                  />
                  <label
                    className={`text-sm ${
                      isDarkMode ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    I consent to donate blood and understand the associated
                    risks and benefits
                  </label>
                </div>
              </div>
            </div>

            {/* Submit Button - Full Width */}
            <div className="col-span-1 md:col-span-2">
              <motion.button
                type="submit"
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 transition-colors duration-200"
              >
                Register as Donor
              </motion.button>
            </div>
          </form>
        </div>
      </div>
    </motion.div>
  );
};

export default DonorRegistration;
